.form-container {
  display: grid;
  grid-template-columns: 1fr; /* Single column layout for small screens */
  gap: 5px; /* Space between inputs */
  width: 100%;
  margin: 0 auto; /* Centers the form */
  padding: 0 16px; /* Adds left and right padding */
}

/* Three columns layout for large screens */
@media (min-width: 768px) {
  .form-container {
    grid-template-columns: repeat(3, 1fr); /* 3 columns */
    max-width: 1200px; /* Prevents it from being too wide */
    padding: 0 32px; /* Adds larger side margins on larger screens */
  }
}

/* Ensure full width of each form field */
.full-width {
  width: 100%;
}

/* Add spacing below each form field */
mat-form-field {
  width: 100%;
}



.order-card {
    width: 95%;
    max-width: 600px;
    margin: 16px auto;
    padding: 16px;
    display: flex;
    flex-direction: column;
    gap: 10px;
    align-items: center; /* Centers content horizontally */
    justify-content: center; /* Centers content vertically */
    min-height: 200px; /* Adjust height as needed */
  }
  
  .full-width {
    width: 100%;
  }
  
  table {
    width: 100%;
    border-collapse: collapse;
  }
  
  .mat-form-field {
    width: 100%;
  }
  
  button {
    align-self: center;
  }
  
  /* Mobile Responsiveness */
  @media (max-width: 600px) {
    .order-card {
      width: 100%;
      padding: 12px;
    }
  }
  .spinner-container {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }  